<template>
    <div class="page-index">
        <van-nav-bar            
            left-arrow
            title="我的关注"
            @click-left="onClickLeft"
        >    
        </van-nav-bar>
        <nav class="tabs flex">
            <p v-bind:class="{'active':tabs == 1}" @click="handleTabs(1)">我的关注</p>
            <p v-bind:class="{'active':tabs == 2}" @click="handleTabs(2)">我的粉丝</p>
        </nav>
        <section v-if="data">
            <van-list
                v-if="data"
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
            >
                <ul class="list">
                    <li  v-for="(item,key) in data" :key="key" >
                        <div class="left">
                            <div class="avatar" > <img :src="item.avatar" > <img class="isvip" src="../../assets/images/icon_isvip.png"  v-if="item.is_ident_icon == 1" > </div>
                            <div class="text">
                                <p class="username" >{{item.username}}</p>
                                <div class="flex flex-none"><p class="level" v-bind:class="{'isCarvip':item.is_carvip == 1}">{{item.level}}</p><p class="extcredits">{{item.extcredits2}}°</p></div>
                            </div>
                        </div>
                        <div class="right">
                            <p class="status" v-if="item.mutual == 1">互相关注</p>
                            <p class="status status-none"  v-if="item.mutual != 1 && item.status == 0  " @click="addFollow(item.uid)">+关注</p>
                            <p class="status"  v-if="item.mutual != 1 && item.status == 1  ">已关注</p>
                        </div>
                    </li>
                </ul>
            </van-list>
        </section>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            },
            tabs:this.$route.query.type,
            user:this.$route.query.user,   // 1: 我的， 2:他的
            data:''
        }
    },
    created(){
        this.init();
    },
    methods:{
        init(){
            if(this.user == 1){
                this.getFollow();
            }else if(this.user == 2){

            }
        },
        addFollow(followuid){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });    
            this.api.addFollow({
                token:this.$store.state.userData,
                followuid:followuid
            }).then(res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.pageInfo.page_index = 0;
                    this.getFollow();

                }else {
                    Toast(res.err_msg);
                }
            })
        },
        // 获取列表
        getFollow(){
            this.api.getFollow({
                token:this.$store.state.userData,
                type:this.tabs,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
                this.loading = false;
                if(res.err_code == 0){
                    
                    if(!res.data.follow_data.length){
                        this.finished = true;
                    }
                    if(this.pageInfo.page_index == 0){
                        this.data = [];
                    }
                    this.data = [...this.data,...res.data.follow_data]
                    this.pageInfo.page_index++;
                }
            })
        },
        onLoad(){
            this.getFollow();
        },
        handleTabs(v){
            this.tabs = v;
            this.pageInfo.page_index = 0;
            this.getFollow();
        },
        onClickLeft(){
            this.$router.go(-1)
        }

    }
}
</script>
